@media only screen and (min-width: 768px) {
    button.more_work {
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        bottom: 60px;
    }
}

@media only screen and (max-width: 768px) {
    .animated {
        visibility: visible !important;
    }
}

