section {
    &>.container {
        overflow: hidden;
    }
    &#intro {
        min-height: 120vh;
        overflow: hidden;
        position: relative;
        padding-top: 25vh;
    }
    &#intro>.bg-black-img,
    &#intro>.overlay {
        position: absolute;
        width: 100%;
        height: 120vh;
    }
    &#intro>.bg-black-img {
        top: 0;
        left: 0;
        /* background: url('../../assets/img/BlackBack.jpg'); */
        // background: url('https://viragesmoto.com/wp-content/uploads/ViragesMoto64sur111-1.jpg');
        // background: url('https://images.unsplash.com/photo-1551034549-befb91b260e0?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60');
        background: url('../img/pages/home/keyboard.webp');
        object-fit: cover;
        background-size: cover;
    }
    &#intro>.overlay {
        left: 0;
        bottom: 0;
        background: linear-gradient( rgba(16, 16, 16, 0) 0%, #101010e0 70%, #060606 100%);
    }
    &#work {
        min-height: 60vh;
    }
    &#skill {
        min-height: 50vh;
        position: relative;
        overflow: hidden;
        & > .container > .row {
            min-height: 50vh;
        }
        &>.bg-skills-img {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: url('../img/pages/home/bg-intro2.jpg');
            transform-origin: 50% 50%;
            background-size: cover;
            filter: brightness(0.5) hue-rotate(1041deg);
        }
        &>.bg-skills-overlay {
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            bottom: 0;
            background: linear-gradient(135deg, rgb(11 8 37 / 14%) 0%, #131d8633 70%);
        }
    }
    &#contact {
        overflow: hidden;
        position: relative;
        min-height: 100vh;
        & > .container > .row {
            min-height: 100vh;
            & > div > div > img {
                border-radius: 4px;
            }
        }
        & > .bg-contact-img {
            filter: blur(10px);
            height: 100%;
            position: absolute;
            width: 100%;
            top: 0;
            right: 0%;
            // right: -60%;
            background-image: url("../img/pages/home/canvas_maserati.jpg");
            object-fit: cover;
            background-size: cover;
        }
    }
}